$primary-color: #3b7b3a;
$secondary-color: #fbbc05;
$tree-color: #16c52d;
$font-stack: Helvetica, sans-serif;

.last_name {
  color: $primary-color;
}
body,
h1,
h2,
p {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: $font-stack;
  line-height: 1.6;
  background-color: #f4f4f4;
}

header {
  text-align: center;
  padding: 24px;
  color: #fff;
background: linear-gradient(135deg, #4a7c59, #5d8a6a);
}

main {
  padding: 24px;
  margin-top: 24px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

footer {
  text-align: center;
  padding: 8px;
  margin-top: 24px;
  color: #fff;
  background-color: $primary-color;
}

h2 {
  color: #333;
  margin-bottom: 8px;
}

nav {
  margin: 0px 0;

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
background: linear-gradient(135deg, #4a7c59, #5d8a6a);
  }

  li {
    float: left;

    a {
      display: block;
      color: white;
      text-align: center;
      padding: 8px 16px;
      text-decoration: none;

      &:hover:not(.active) {
        background-color: $secondary-color;
      }
    }
  }
}

.container {
  width: 80%;
  margin: auto;
  overflow: hidden;
}

.profile-picture {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 24px auto;
  background-color: $secondary-color;
}

.active {
  background-color: $secondary-color;
}